
import React, { useEffect, useState } from 'react'

//

// import cardFormatter from './cardFormatter/cardFormatter'



function useInput({
    className, type, inputMode, inputEvent,
    bankCurrencyActiveType, bankWithdrawalValue
}) {

    //

    const [value, setValue] = useState(
        inputEvent === 'blockchainCurrency'
            ? '5000'
            : inputEvent === 'bankCurrency' ? '9' : ''
    );
    const [error, setError] = useState(null);
    const [errorInputValue, setErrorInputValue] = useState(null)
    const [isBlur, setBlur] = useState(null)
    //

    useEffect(
        () => {
            if     (inputEvent === 'blockchainCurrency' && value < 5000) setError("Min 5000 usdt");
            else if(inputEvent === 'bankCurrency'       && value < 1) setError("Min 1 usdt");
            else setError(null);
        }, [value]
    )

    function isValidEmail() {
        return /\S+@\S+\.\S+/.test(value);
    }


    useEffect(
        () => {
            
            if(inputEvent === 'mainInput' ) {
                if ( value.length !== 0 && !isValidEmail(value)) {
                    setErrorInputValue('Email is invalid');
                } else {
                    setErrorInputValue(null);
                }
            } 
            else if (inputEvent === 'userTelegram'){
                if (value.length < 5 && value.length !== 0) {
                    setErrorInputValue('Telegram username must be more 5 characters');
                } else {
                    setErrorInputValue(null);
                }
            }

            else if(value.length < 5 && value.length != 0){
                setErrorInputValue('Min 5 characters');
            } 
            
            else setErrorInputValue(null)
        }, [value]

    )

    //

    const inputsFieldNotePast = (value) => {
        console.log('value?.length >= 5', value?.length >= 5)
        if(value?.length <= 5){
            setBlur('Min 5 characters')
        } else setBlur(null)
    }

    let amountDepositValue

    const input =
        <input
            className={className}
            value={
                inputEvent === 'bankCurrency' 
                    ? bankWithdrawalValue
                    : value
            }
            // maxLength={+assetDataOneReserves?.decimals}
            onChange={ e => setValue( e.target.value ) }
            type={type}
            name='userNumber'
            inputMode={inputMode}
            // onBlur={() => setBlur('dadas')}
        />



    //

    return [
        input,
        // error,
        amountDepositValue = value,
        errorInputValue,
        isBlur,
        inputsFieldNotePast
    ];

}



export default useInput